import * as React from "react";
import "./less/snapshot.less";
import MetricsBlock from "./metrics-block";

export class Snapshot extends React.Component<{
  title?: string;
  description?: string;
  slug?: string;
  contentScore?: number;
  totalPlays?: number;
  tags?: [];
  poster?: string;
  highlight?: boolean;
  isVisible?: boolean;
  handleDetail?: (title: string) => void;
  onClick?: (event: React.MouseEvent<HTMLButtonElement>) => void;
}> {

  componentDidMount() {
    this.handleVideo();
  }

  componentDidUpdate(props:any) {
    this.handleVideo();
  }

  handleVideo = () => {
    const video = document.getElementById(`video-${this.props.slug}`) as HTMLVideoElement;
    if (!video) { return; }
    this.props.isVisible ? video.play() : video.pause();
  }

  render() {
    const { poster, highlight, title, slug, totalPlays, contentScore } = this.props;
      return (
        <div
          className={`app-snapshot ${highlight ? "-highlight" : ""}`}
          id={slug}
          onClick={() => this.props.handleDetail(title)}
        >
          {highlight && (
            <div className="app-snapshot__highlighted">
              <h3>Top Performer</h3>
              <div className="app-snapshot__title">{title}</div>
              <div className="app-snapshot__badge">
                <img src={process.env.PUBLIC_URL + `/blob.png`} />
              </div>
            </div>
          )}
          <div
            className="app-snapshot__thumb"
            style={{
              backgroundImage: `url(${poster})`
            }}
          >
            {poster.indexOf(".mp4") !== -1 && (
              <video
                id={`video-${slug}`}
                className="app-snapshot__video"
                src={poster}
                muted
                loop
                playsInline
              />
            )}
          </div>
          <div className="app-metrics">
            <div className="app-metrics__plays">
              <MetricsBlock metric={totalPlays} label="Plays" />
            </div >
            <div className="app-metrics__score">
              <MetricsBlock percent={contentScore} label="Score" />
            </div>
          </div>
          <div className="app-snapshot__title">{!highlight && title}</div>
        </div>
      );
    }
  
}



