import * as React from "react";
import "./less/app-detail.less";
import MetricsBlock from "./metrics-block";
// import { Metrics } from "./metrics";
import { PuiIcon, PuiButton, SizeModifier } from "@pui/react";

export class AppDetail extends React.Component<{
  title?: string;
  totalPlays?: number;
  contentScore?: number;
  adImpressions: number
  completeRate?: number;
  description?: string;
  embeds?: number;
  playRate?: number;
  poster?: string;
  tags?: [],
  closeDetail?: (event: React.MouseEvent<HTMLDivElement>) => void;
}>{

  state = { 
    cpm: 5, 
    showRoi: false,
    basic: true
  }

  showRoi = () => this.setState({ showRoi: true });

  formatString = (num: number) => num && num.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",");

  handleClose = (e: React.MouseEvent<HTMLDivElement>) => {
    this.props.closeDetail && this.props.closeDetail(e);
    this.setState({ showRoi: false, basic: true });
  }

  handleCpm = (e: React.FormEvent<HTMLInputElement>) => {
    const cpm = e.currentTarget.value;
    this.setState({ cpm });
  }

  selectAds = (e: React.MouseEvent<HTMLButtonElement>) => { 
    e.preventDefault();
    this.setState({ basic: false });
  }

  selectBasic = (e: React.MouseEvent<HTMLButtonElement>) => {
    e.preventDefault();
    this.setState({ basic: true });
  }

  render() {
    const { poster, title, description, adImpressions, contentScore, playRate, totalPlays, completeRate } = this.props;
    const { cpm, showRoi, basic } = this.state;
    const roi = cpm * (adImpressions / 1000);

    return (
      <div className="app app-detail">

        <div className="app-detail__close" onClick={this.handleClose}>
          <PuiIcon name="cross" size={SizeModifier.m} />
        </div>

        {poster && (
          <div
            className="app-detail__thumb"
            style={{
              backgroundImage: `url(${poster})`
            }}>
            { (poster.indexOf('.mp4') !== -1) &&
              <video 
                className="app-detail__video" 
                src={poster} 
                autoPlay 
                muted 
                loop 
                playsInline 
              />
            }
          </div>
        )}

        <div className="app-detail__wrap">

          <div className="app-detail__toggles">
            <button className={`app-detail__button${basic ? ' -is-active':''}`} onClick={this.selectBasic}>Basic</button>
            <button className={`app-detail__button${!basic ? ' -is-active' : ''}`} onClick={this.selectAds}>$$$$</button>
          </div>

          <h1>{title}</h1>

          { basic && 
          <>
            <p>{description}</p>
            <div className="app-detail__metrics">
              <div className="app-detail__metrics--score">
                <MetricsBlock percent={contentScore} label="Content Score" />
                <div className="app-detail__metrics--score-secondary">
                  <MetricsBlock percent={playRate} label="Play Rate" secondary={true} />
                  <MetricsBlock percent={completeRate} label="Complete Rate" secondary={true} />
                </div>
              </div>
              <div className="app-detail__metrics--plays">
                <MetricsBlock metric={totalPlays} label="Total Plays" />
              </div>
            </div>
          </>
          }

          { !basic && 
            <div className="app-detail__metrics">
              <div className="app-detail__metrics--ads">
                <MetricsBlock metric={adImpressions} label="Ad Impressions" />
              </div>
              <label>Know your CPM?</label>
              <div className="app-detail__form">
                <div className="app-detail__input-spoof">
                  <PuiIcon name={"monetize"} size={SizeModifier.s} />
                  <input
                    type="number"
                    placeholder="CPM"
                    onChange={this.handleCpm}
                    value={cpm}
                  />
                </div>
                <PuiButton type="primary" onClick={this.showRoi}><span>Calculate ROI</span></PuiButton>
              </div>
              { showRoi && 
                <div className="app-detail__roi-wrap">
                  <div className="app-detail__roi">= <span>$ {this.formatString(roi)}</span></div>
                  <div className="app-detail__roi-description">CPM x (Ad Impressions / 1000)</div>
                </div>
              }
            </div>
          }
        </div>
      </div>
    );
  }
}



