import * as React from "react";
import ReactJWPlayer from 'react-jw-player';

export class JWPlayer extends React.Component<{
  playlist: string;
  playerId: string;
  seekTo?: number;
  getDuration?: (duration:number) => void;
}> {

  onReady = ()  => {
    const player = window.jwplayer(this.props.playerId);
    const duration = player.getDuration();
    this.props.getDuration && this.props.getDuration(duration);
  }

  render() {
    const { playlist, playerId } = this.props;
    return (
      <ReactJWPlayer
        playlist={playlist}
        onReady={this.onReady}
        playerId={playerId}
        playerScript={`https://cdn.jwplayer.com/libraries/${playerId}.js`}
      />
    );
  }
}



