import * as React from 'react';
import "./less/welcome.less";
import { Link } from "react-router-dom";
import { LogoFull } from "../components/logo/logo-full";
import { VideosIcon } from "./videos-icon";
import PulseButton from "../components/pulse-button/pulse-button";

export class AppWelcome extends React.Component<{
}>{
  render() {
    return (
      <div className="app app-welcome">
        <div className="app-welcome__hero">
          <div className="app-welcome__logo">
            <LogoFull />
          </div>
          <div className="app-welcome__intro">
            <VideosIcon />
            <h1>Smol Dash</h1>
            <h2>A mobile analytics app for <br />content creators</h2>
          </div>
        </div>
        <div className="app-welcome__user">
          <h3>
            Welcome, <span>friend@moneymakr.me</span>
          </h3>
          <Link to="app/feed">
            <PulseButton />
          </Link>
        </div>
      </div>
    );
  }
}


