import * as React from "react";

export const Typography = () => (
    <div className={`ds-page`}>
      <h1>h1: Chapter 1. Down the Rabbit Hole, Pool of Tears</h1>
      <h2>
        h2: Alice was beginning to get very tired of sitting by her sister on
        the bank, and of having nothing to do.
      </h2>
      <h3>
        h3: Once or twice she had peeped into the book her sister was reading,
        but it had no pictures or conversations in it
      </h3>
      <h4>h4: And was just in time to see it pop down a large rabbit-hole</h4>
      <h5>
        h5: So she was considering in her own mind as well as she could, for
        the hot day made her feel very sleepy and stupid
      </h5>
      <p>
        p: So she was considering in her own mind as well as she could, for
        the hot day made her feel very sleepy and stupid,
        whether the pleasure of making a daisy chain would be worth the
        trouble of getting up and picking the daisies, when suddenly a
        White Rabbit with pink eyes ran close by her. There weas nothing
        so very remarkable in that; nor did Alice think it so very much
        out of the way to hear the Rabbit say to itself
      </p>
      <p>
        p: White Rabbit with pink eyes ran close by her. There weas nothing
        so very remarkable in that; nor did Alice think it so very much
        out of the way to hear the Rabbit say to itself
      </p>
      <ul>
        <li>Remarkable in that</li>
        <li>So she was considering in</li>
        <li>Alice</li>
        <li>Say to itself</li>
      </ul>

      <span>This is a plain span</span>

      <p>Here there's a <span>span in a paragraph</span>, for reference.</p>

      <p>Paragraph text goes here. <a href="#">Link in a paragraph tag</a> More regular paragraph text.</p>

      <a href="#">Stand-alone link, </a>

      <span><a href="#"> and a link within a span</a></span>

      <br/>
      <br/>

      <small>
        small: So she was considering in her own mind as well as she could, for
        the hot day made her feel very sleepy and stupid, whether the
        pleasure of making a daisy chain would be worth the trouble of
        getting up and picking the daisies, when suddenly a White Rabbit
        with pink eyes ran close by her. There weas nothing so very
        remarkable in that; nor did Alice think it so very much out of the
        way to hear the Rabbit say to itself
      </small>

    </div>
);



