export const toTitleCase = (str:string) => {
  str = str.toLowerCase()
    .split(' ')
    .map((s) => s.charAt(0).toUpperCase() + s.substring(1))
    .join(' ');
  return str;
}

export const getFirstLetter = (str:string) => str.charAt(0).toUpperCase();


