import * as React from 'react';
import "./less/metrics-block.less";

const MetricsBlock = (props:any) => {

  const { percent, metric, secondary, label } = props;
  const toPercentage = (num: number) => num && `${Math.floor(num * 100)}%`;
  const formatString = (num: number) => num && num.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",");
  const value = percent ? toPercentage(percent) : formatString(metric);

  return (
    <div className="app-metrics-block">
      {!secondary && value}
      <span> {label} </span>
      {secondary && value}
    </div>
  )
}

export default MetricsBlock;



