import "./dashboard.less";
import * as React from "react";
import { BrowserRouter as Router, Route, Switch } from "react-router-dom";

import {
  SideNav,
  TopNav,
  LayoutTester,
  Footer,
  Home,
  Typography,
  VideoDetail,
  NotFound,
  App,
  AppWelcome,
  Analytics,
} from "../../index";

export class Dashboard extends React.Component<{}> {
  state = {
    collapsed: false,
    theme: "light"
  };

  handleTheme = () => {
    if (this.state.theme === "light") {
      this.setState({ theme: "dark" });
    } else {
      this.setState({ theme: "light" });
    }
  };

  handlePageMargins = (navCollapsed: boolean) => {
    this.setState({ collapsed: navCollapsed });
  };

  render() {
    const { collapsed, theme } = this.state;

    return (
      <div className="simi-dashboard">
        <Router>
          <Switch>
            <Route exact path="/">
              <div className="simi-main-wrap">
                <SideNav
                  handleNavState={this.handlePageMargins}
                  theme={theme}
                />
                <div
                  className={`simi-main-wrap__content ${
                    collapsed ? "nav-collapsed" : ""
                  }`}
                >
                  <TopNav toggleTheme={this.handleTheme} />
                  <Home />
                </div>
                <Footer />
              </div>
            </Route>
            <Route path="/page">
              <div className="simi-main-wrap">
                <SideNav
                  handleNavState={this.handlePageMargins}
                  theme={theme}
                />
                <div
                  className={`simi-main-wrap__content ${
                    collapsed ? "nav-collapsed" : ""
                  }`}
                >
                  <TopNav toggleTheme={this.handleTheme} />
                  <Route path="/page/videos" component={VideoDetail} />
                  <Route path="/page/analytics" component={Analytics} />
                </div>
                <Footer />
              </div>
            </Route>
            <Route path="/sandbox">
              <div className="simi-main-wrap">
                <SideNav
                  handleNavState={this.handlePageMargins}
                  theme={theme}
                />
                <div
                  className={`simi-main-wrap__content ${
                    collapsed ? "nav-collapsed" : ""
                  }`}
                >
                  <TopNav toggleTheme={this.handleTheme} />
                  <Route
                    exact
                    path="/sandbox/layout-tester"
                    component={LayoutTester}
                  />
                  <Route
                    exact
                    path="/sandbox/typography"
                    component={Typography}
                  />
                </div>
                <Footer />
              </div>
            </Route>
            <Route exact path="/app" component={AppWelcome} />
            <Route exact path="/app/feed" component={App} />
            {/* <Route
              path="/app/detail/:slug"
              render={(routeProps: any) => <AppDetail {...routeProps} />}
            /> */}
            <Route component={NotFound} />
          </Switch>
        </Router>
      </div>
    );
  }
}



