import * as React from "react";
import { PuiThumbnailListItem } from '@pui/react/components/media/thumbnail-list-item';
import { PuiListItem } from '@pui/react/components/lists-and-tables/list-item';
import { PuiIcon } from '@pui/react/components/general/icon';
import { PuiProgressBar } from '@pui/react/components/progress-and-feedback/progress-bar';
import { SizeModifier } from '@pui/react';
import './home.less';

export class Home extends React.Component<{
}>{

  renderThumbs = () => {
    const links = ['Embed', 'View Analytics', 'Share', 'Publish'];
    const thumbs = [
      "https://picsum.photos/600/300",
      "https://picsum.photos/400/400",
      "https://picsum.photos/500/300",
      "https://picsum.photos/200/400",
      "https://picsum.photos/500/800",
      "https://picsum.photos/500/200",
    ];
    return thumbs.map((thumb: any, i: number) => (
      <PuiThumbnailListItem key={i}
        src={thumb}
        title="Top 10 Vacation Destinations"
        subtitle="Jan 04, 2019"
        blur={true}
        badge={1502}
        moreMenuChildren={
          <React.Fragment>
            {links.map((link: any, index: number) => <PuiListItem label={link} key={index} />)}
          </React.Fragment>}
      />
    ))
  }

  renderStats = () => {
    const stats = [
      {
        title: "Plays",
        number: "249,000"
      },
      {
        title: "Ad Impressions",
        number: "20"
      },
      {
        title: "Streaming - VOD",
        number: "2,499.GB"
      },
      {
        title: "Streaming - Live",
        number: "2.8 M"
      },
      {
        title: "Storage",
        number: "2.8 M"
      }
    ];
    return stats.map((block: any, i: number) => (
      <div key={i} className="page--home__stats-block">
        <h5>{block.title}</h5>
        <h2>{block.number}</h2>
        <div>of 250,000</div>
        <PuiProgressBar progressValue={.69} />
      </div>
    ))
  };

  render() {
    return (
      <div className="ds-page page--home">
        <h2>Welcome, Kim</h2>
        <section>
          <h3>
            <a href="#">
              Recent Videos
              <PuiIcon name="chevron-right" size={SizeModifier.xs} />
            </a>
          </h3>
          <div className="page--home__thumbs">{this.renderThumbs()}</div>
        </section>
        <div className="ds-row">
          <section>
            <h3>
              <a href="#">
                Usage (Month-to-Date)
                <PuiIcon name="chevron-right" size={SizeModifier.xs} />
              </a>
            </h3>
            <div className="ds-row">{this.renderStats()}</div>
          </section>
          <section className="page--home__links">
            <h3>Quick Links</h3>
            <h4><a href="#">Account</a></h4>
            <h4><a href="#">Billing</a></h4>
            <h4><a href="#">Support</a></h4>
            <p>Search our articles, give feedback, and learn more about JW Player</p>
            <h4><a href="#">Developer</a></h4>
            <p>Join the community, view demos, see references, dev guides, and tools</p>
          </section>
        </div>
      </div>
    );
  }
}


