import * as React from "react";
import { Dashboard } from "./js/pages/dashboard/dashboard";
import { Login } from "./js/pages/login/login";

import "./less/build.less";

export default class App extends React.Component {

  render() {
    const user = true;

    return (
      <div className="App">
        { user && <Dashboard /> }
        { !user && <Login /> }
      </div>
    )
  }
}



