import * as React from "react";
import { PuiIcon } from "@pui/react";
import "./pulse-button.less";

const PulseButton = () => {
  return (
    <div className="pulse-button">
      <div className="pulse-rings outer">
        <div className="pulse-rings inner">
          <div className="pulse-rings innermost">
            <div className="pulse-rings button">
              <PuiIcon name="arrow-right" />
            </div>
          </div>
        </div>
      </div>
    </div>
  )
}

export default PulseButton;



