import * as React from "react";
// import { Grid } from "../grid/grid";

export class LayoutTester extends React.Component<{
}>{

  state = {
    debug: true,
    layout: "fluid",
  }

  toggleDebug = (event: any) => {
    const checked = event.currentTarget.checked;
    this.setState({ debug: checked });
  };

  toggleLayout = (event:any) => {
    const layout = event.currentTarget.value;
    this.setState({ layout });
  }

  render() {
    const { debug, layout } = this.state;

    return (
      <div className={`ds-page ds-page--${layout} -debug-${debug ? "on" : "off"}`}>
        <h1>Layout Tester</h1>
        {/* <Grid /> */}
        <div className="simi-control-panel">
          <div className="simi-control-panel__section">
            <label>Debug Mode:</label>
            <input type="checkbox" checked={debug} onChange={this.toggleDebug} />
          </div>
          <div className="simi-control-panel__section">
            <label>Layout:</label>
            <select value={layout} className="layout" onChange={this.toggleLayout}>
              <option value="fluid">Fluid</option>
              <option value="fixed">Fixed</option>
              <option value="full">Full</option>
            </select>
          </div>
        </div>
      </div>
    );
  }
}



