import * as React from "react";
import "./less/app.less";
import { VideosIcon } from "./videos-icon";
import musicManifest from "./manifests/music-small.json";
import customerManifest from "./manifests/apartment-therapy.json";
import TrackVisibility from 'react-on-screen';

import { AppDetail } from "./app-detail";
import { TopNav } from "./top-nav";
import { Snapshot } from "./snapshot";
import { PuiIcon, PuiTag, PuiButton, SizeModifier } from "@pui/react";

interface AppState {
  filterOpen: boolean;
  activeProperty: string;
  currentFilter: string;
  detailModalOpen: boolean;
  detailPage: {
    title?: string;
    totalPlays?: number;
    contentScore?: number;
    adImpressions: number
    completeRate?: number;
    description?: string;
    embeds?: number;
    playRate?: number;
    poster?: string;
    tags?: []
  }
}

export class App extends React.Component<{}, AppState>{

  constructor(props:any) {
    super(props);
    this.state = {
      activeProperty: 'Apartment Therapy',
      filterOpen: false,
      currentFilter: '',
      detailModalOpen: false,
      detailPage: null
    };
  }

  componentDidMount() { 
    this.setProperty(); 
  }

  componentDidUpdate(props:any, newProps:any) { 
    if (newProps.activeProperty !== this.state.activeProperty) {
      this.setState({ currentFilter: ''})
    }
  }

  setProperty = () => this.state.activeProperty === 'Fader TV' ? musicManifest : customerManifest;

  updateProperty = (activeProperty: string) => this.setState({ activeProperty });

  openFilterMenu = () => this.setState({ filterOpen: true });

  closeFilterMenu = () => this.setState({ filterOpen: false });

  renderSnapshots = () => {
    const manifest = this.setProperty();
    return manifest
      .filter((video:any) => {
        const { currentFilter } = this.state;
        if (currentFilter.length) {
          return video.tags.includes(currentFilter)
        } else {
          return video;
        }
      })
      .sort((a:any, b:any) => a.contentScore - b.contentScore)
      .reverse()
      .map((video:any, i:number) => 
      <TrackVisibility key={i} partialVisibility>
        <Snapshot 
          slug={video.slug}
          title={video.title} 
          description={video.description} 
          totalPlays={video.totalPlays} 
          tags={video.tags} 
          poster={video.poster} 
          contentScore={video.contentScore} 
          highlight={i === 0}
          handleDetail={this.openDetailModal} 
        />
      </TrackVisibility>
    )
  };

  openDetailModal = (title:any) => {
    const manifest = this.setProperty();
    const match = manifest.filter((item: any) => item.title === title)[0];
    match && this.setState({ 
      detailModalOpen: true,
      detailPage: match 
    });
  }

  closeDetailModal = () => {
    this.setState({
      detailModalOpen: false,
      detailPage: null
    });
  }

  selectFilter = (tag:any) => {
    this.setState({ 
      currentFilter: tag,
      filterOpen: false
    });
  }

  removeFilter = () => {
    this.setState({ 
      currentFilter: '',
      filterOpen: false
    })
  }

  renderTagSections = () => {
    const allTags:any[] = [];
    const categories:any[] = [];
    const manifest = this.setProperty();

    manifest.map((obj:any) => allTags.push(...obj.tags));

    allTags.forEach((element:any, i:number) => {
      if (allTags.indexOf(element, i + 1) > -1) {
        if (categories.indexOf(element) === -1) {
          categories.push(element);
        }
      }
    });
    
    return categories.map((tag:any, i:number) => {
      const count = allTags.filter((v) => (v === tag)).length;
      return (
        <div key={i} className="app__filter-menu__tag-section" onClick={() => this.selectFilter(tag)}>
          <PuiTag tag={tag} />
          <label>{count} videos</label>
        </div>
      );
    })
  }

  renderFilterMenu = () => {
    return ( 
      <div className={`app__filter-menu ${this.state.filterOpen ? '-is-open' : ''}`}>
        <div className="app__filter-menu__content">
          <div className="app__filter-menu__close" onClick={this.closeFilterMenu}>
            <PuiIcon name="cross" size={SizeModifier.s} />
            Filter by tag
          </div>
          <div className="app__filter-menu__tags">
            {this.renderTagSections()}
          </div>
        </div>
      </div>
    )
  }

  render() {
    const { filterOpen, currentFilter, detailModalOpen, detailPage } = this.state;
    return (
      <div className={`app ${filterOpen ? "-is-locked" : ''}`}>
        <TopNav handleProperty={this.updateProperty} />
        <div className="app__main">
          <div className={`app__filter ${currentFilter.length ? '-on' :''}`}>
            { currentFilter.length ? 
              <div>
                <VideosIcon />
                <div>
                  <h2>Filtered: <PuiTag tag={currentFilter} /></h2>
                  <PuiButton type="tertiary" onClick={this.removeFilter}>
                    <PuiIcon name="cross" size={SizeModifier.xs} />
                    <span>Clear</span>
                  </PuiButton>
                </div>
              </div> 
              : 
              <div onClick={this.openFilterMenu}>
                <VideosIcon />
                <h2>Filter</h2>
              </div>
            }
          </div>
          {this.renderFilterMenu()}
          <div className="app__videos">
            {this.renderSnapshots()}
          </div>
        </div>
        <div className={`app__detail-wrap ${detailModalOpen ? '-is-open' : ''}`}>
          <AppDetail {...detailPage} closeDetail={this.closeDetailModal} />
        </div>
      </div>
    )
  }
}



