import './top-nav.less';
import * as React from "react";
import { PuiIcon, SizeModifier, PuiMenuPane, PuiMoreMenu, PuiButton } from '@pui/react';

export class TopNav extends React.Component<{
  theme?: string;
  toggleTheme?: (event: React.MouseEvent<HTMLSpanElement>) => void
}>{

  state = {
    propsMenuOpen: false,
    activeProperty: "Design Team",
    settingsMenuOpen: false
  }

  settingsItems = [
    { name: "Properties", link: "/" },
    { name: "Usage", link: "/" },
    { name: "API credentials", link: "/" },
    { name: "Users", link: "/" },
    { name: "Profile", link: "/" },
    { name: "Sign Out", link: "/" },
  ]

  sampleProperties = [
    "Design Team", 
    "Jenkins Test", 
    "JW Player", 
    "This is a Very Long Title For A Property Please Shorten Me"
  ]

  togglePropsMenu = () => {
    const { propsMenuOpen } = this.state;
    this.setState({ propsMenuOpen: !propsMenuOpen });
  }

  toggleActiveProperty = (event: React.MouseEvent<HTMLElement>) => {
    this.setState({
      activeProperty: event.currentTarget.innerHTML,
      propsMenuOpen: false
    })
  }

  toggleSettingsMenu = (event: React.MouseEvent<HTMLElement>) => {
    const { settingsMenuOpen } = this.state;
    this.setState({ settingsMenuOpen: !settingsMenuOpen });
  }

  render() {
    const { activeProperty, propsMenuOpen } = this.state;
    return (
      <div className="simi-top-nav">
        <div className="simi-top-nav__toggles">
          <PuiButton>
            <span>Add Video</span>
          </PuiButton>
          <div className="simi-top-nav__select-wrap">
            <div
              className="simi-top-nav__select"
              onClick={this.togglePropsMenu}
            >
              <span>{activeProperty}</span>
              <PuiIcon
                name={propsMenuOpen ? "chevron-up" : "chevron-down"}
                size={SizeModifier.s}
              />
            </div>
            <PuiMenuPane isOpen={propsMenuOpen} anchor={["bottom"]}>
              {this.sampleProperties
                .filter(prop => prop !== this.state.activeProperty)
                .sort()
                .map((property, i) => (
                  <a
                    className="simi-top-nav__item"
                    key={i}
                    onClick={this.toggleActiveProperty}
                  >
                    {property}
                  </a>
                ))}
            </PuiMenuPane>
          </div>
        </div>
        <div
          className="simi-top-nav__settings"
          onClick={this.toggleSettingsMenu}
        >
          <span>Kim Hart</span>
          <PuiMoreMenu type="icon" icon="settings">
            {this.settingsItems.map((item: any, index: number) => (
              <a className="simi-top-nav__item" key={index}>
                {item.name}
              </a>
            ))}
            <a className="simi-top-nav__item" onClick={this.props.toggleTheme}>
              Toggle Theme
            </a>
          </PuiMoreMenu>
        </div>
      </div>
    );
  }
}



