import './login.less';
import * as React from "react";
import { Footer } from "../../components/footer/footer";
// import TextInput from "../../components/TextInput";

const navItems = [
  {
    href: "https://www.jwplayer.com/careers",
    name: "Careers"
  },
  {
    href: "https://www.jwplayer.com",
    name: "Company"
  },
  {
    href: "https://medium.com/jw-player-engineering",
    name: "Blog"
  },
  {
    href: "https://support.jwplayer.com",
    name: "Support"
  },
  {
    href: "https://developer.jwplayer.com",
    name: "Developer"
  }
];

export class Login extends React.Component{
  render() {
    return (
      <div className="page layout--fluid page--login">
        <div className="login-header">
          <div className="logo-wrap">
            <img className="mark" src="//developer.jwplayer.com/img/jw-logo-mark-white.svg" />
            <img src="//developer.jwplayer.com/img/jw-logo-text-white.svg" />
          </div>
          <ul className="nav-links">{
            navItems.map((item, i) => {
              return (
                <li key={i}>
                  <a href={item.href}>{item.name}</a>
                </li>
              )
            })
          }</ul>
        </div>
        <div className="login-main">
          <div className="login-prompt-box">
            <h1>Log In to JW Player</h1>
            <p>
              Don't have a JW Player account?&nbsp;
              <a href="https://www.jwplayer.com/sign-up/">Sign Up</a>
            </p>
          </div>
        </div>
        <Footer />
      </div>
    )
  }
}



