import './side-nav.less';
import * as React from "react";

import * as format from "../../../utils/format";
import navConfig from "../../../configs/sidenav-config.json";

import { Link } from "react-router-dom";
import { PuiIcon, SizeModifier } from "@pui/react";
import { LogoFull } from "../../components/logo/logo-full";


export class SideNav extends React.Component<{
  theme?: string;
  globalStatusOpen?: boolean;
  handleNavState: (event: boolean) => void;
}> {

  state = {
    status: {
      indicator: '',
      description: ''
    },
    isCollapsed: false,
    propsMenuOpen: false,
    activeProperty: "Design Team",
    subItemsOpen: false,
  }

  componentDidMount() {
    this.getSystemStatus();
  }

  getSystemStatus = () => {
    fetch("https://9chp34zg69jm.statuspage.io/api/v2/status.json")
      .then(response => response.json())
      .then(json => this.setState({ status: json.status }));
  }

  toggleCollapse = () => {
    const { isCollapsed } = this.state;
    this.props.handleNavState(!isCollapsed);
    this.setState({ isCollapsed: !isCollapsed });
  }

  toggleSubItems = (event: React.MouseEvent<HTMLDivElement>) => {
    const { subItemsOpen } = this.state;
    this.setState({ subItemsOpen: !subItemsOpen });
  }

  handleOpen = (event: React.MouseEvent<HTMLDivElement>) => {
    const { isCollapsed } = this.state;
    if (!isCollapsed || event.currentTarget.classList.contains("simi-side-nav-category--home")) {
      return; 
    }
    event.currentTarget.classList.add('-is-open');
  }

  handleClose = (event: React.MouseEvent<HTMLDivElement>) => {
    const { isCollapsed } = this.state;
    if (!isCollapsed) { return; }
    event.currentTarget.classList.remove('-is-open');
  }

  listCategories = () => {
    const { isCollapsed, subItemsOpen } = this.state;
    return navConfig.categories
      .filter((category: any) =>
        !isCollapsed ? category.icon !== "home" : category
      )
      .map((category: any, i: number) => (
        <div
          className={`simi-side-nav__category simi-side-nav-category--${
            category.icon
          }`}
          key={i}
          onMouseEnter={this.handleOpen}
          onMouseLeave={this.handleClose}
        >
          <Link to={`/${category.link || ''}`}>
            <PuiIcon name={category.icon} size={SizeModifier.s} />
          </Link>
          <div className={`simi-side-nav__items`}>
            {category.items.map((item: any, index: number) => (
              <div
                className={`simi-side-nav__item ${
                  item.subItems && subItemsOpen ? "-is-open" : ""
                }`}
                key={index}
                onClick={item.subItems && this.toggleSubItems}
                title={item.name}
              >
                <Link to={item.link ? item.link : "/"}>
                  {format.toTitleCase(item.name)}
                  {item.subItems && (
                    <PuiIcon name="chevron-up" size={SizeModifier.xs} />
                  )}
                </Link>
                {item.subItems &&
                  subItemsOpen &&
                  item.subItems.map((subItem: any, key: number) => {
                    return (
                      <div
                        className="simi-side-nav__sub-item"
                        key={key}
                        title={subItem.name}
                      >
                        {format.toTitleCase(subItem.name)}
                      </div>
                    );
                  })}
              </div>
            ))}
          </div>
        </div>
      ));
  }

  render() {
    const { isCollapsed, status } = this.state;
    const { theme } = this.props;

    return (
      <div className={`simi-side-nav ${isCollapsed ? "-is-collapsed" : ""} -theme-${theme}`}>
        <div className="simi-side-nav__header">
          <div className="simi-side-nav__toggle" onClick={this.toggleCollapse}>
            <PuiIcon name="list-big" size={SizeModifier.s} />
          </div>
          {!isCollapsed && <Link to="/"><LogoFull /></Link>}
        </div>
        <div className="simi-side-nav__categories">{this.listCategories()}</div>
        <div className={`simi-side-nav__system-status ${isCollapsed ? '-simple' : ''}`}>
          <div className={`indicator ${status.indicator}`} />
          {!isCollapsed && <span className="description">{status.description}</span>}
        </div>
      </div>
    );
  }
}


