import * as React from "react";
import "./video-detail.less";
import { JWPlayer } from "../../components/jw-player/jw-player";
import moment from "moment"

export class VideoDetail extends React.Component {
  state = {
    mediaId: "Atr3eiyC",
    title: "",
    startValue: 0,
    endValue: 0,
    startMin: 0,
    endMax: 0
  };

  private mediaIdInputRef = React.createRef<HTMLInputElement>();

  componentDidMount() {
    this.setState({ mediaId: this.getRandomMediaId() });
    this.getMetadata(this.state.mediaId);
  }

  componentDidUpdate() {
    this.getMetadata(this.state.mediaId);
  }

  getMetadata = (mediaId:string) => {
    fetch(`https://cdn.jwplayer.com/v2/media/${mediaId}`)
      .then(res => res.json())
      .then(json => this.setState({ title: json.title }));
  }

  getRandomMediaId = () => {
    const mediaIds = [
      "Atr3eiyC",
      "1mN4aZX9",
      "GRQwaYyD",
      "I7iXgbwg",
      "T9wjbJYh",
      "wIBhcD7R",
      "AnvpySBe",
      "o2RM8wW8",
      "oSRD4xzP",
      "OoLuKncy",
      "1fWd3uGZ",
      "YJSvRKAB",
      "RgPbOpnk",
      "7kbHNsVs",
      "JTqHkAyO"
    ];
    return mediaIds[Math.floor(Math.random() * mediaIds.length)];
  };

  updateDuration = (duration: number) => {
    this.setState({
      endMax: duration,
      endValue: duration
    });
  };

  convertSeconds = (seconds:any) => moment.utc(seconds * 1000).format('HH:mm:ss');

  handleMediaId = (e: any) => this.setState({ mediaId: e.target.value });

  handleDragStart = (e: React.FormEvent<HTMLInputElement>) => {
    const startValue = parseInt(
      (e.target as HTMLInputElement).value, 10
    );
    const { endValue, endMax } = this.state;

    this.setState({ startValue });

    if (startValue > endValue - 1) {
      this.setState({ endValue: startValue + 1 });

      if (endValue === endMax) {
        this.setState({ startValue: endMax - 1 });
      }
    }
  };

  handleDragEnd = (e: React.FormEvent<HTMLInputElement>) => {
    const endValue = parseInt(
      (e.target as HTMLInputElement).value, 10
    );
    const { startValue, startMin } = this.state;

    this.setState({ endValue });

    if (endValue < startValue + 1) {
      this.setState({ startValue: endValue - 1 });

      if (endValue === startMin) {
        this.setState({ endValue: 1 });
      }
    }
  };

  render() {
    const { mediaId, startValue, endValue, title } = this.state;
    return (
      <div className="ds-page ds-page--full page--video-detail">
        <div className="page--video-detail__title">
          <h2>{title}</h2>
          <div className="page--video-detail__media-id">
            <label>Media ID:</label>
            <input
              onChange={this.handleMediaId}
              ref={this.mediaIdInputRef}
              type="text"
              placeholder="Enter Media ID"
              value={mediaId}
            />
          </div>
        </div>
        {/* <div className="page--video-detail__tabs">
        </div> */}
        <div className="page--video-detail__main">
          <div className="page--video-detail__spotlight">
            <h4>Video Summary</h4>
            <div className="page--video-detail__metadata-line">
              <p>Media ID:</p>
              <div className="code">{mediaId}</div>
            </div>
            <div className="page--video-detail__metadata-line">
              <p>Status:</p>
              <div className="status">Ready</div>
            </div>
            <div className="page--video-detail__metadata-line">
              <p>Type:</p>
              <div className="text">Hosted Video</div>
            </div>
            <div className="page--video-detail__metadata-line">
              <p>Duration:</p>
              <div className="code">{this.convertSeconds(endValue)}</div>
            </div>
          </div>
          <div className="page--video-detail__video">
            <JWPlayer
              playerId="dixOo4Dn"
              playlist={`https://cdn.jwplayer.com/v2/media/${mediaId}`}
              getDuration={this.updateDuration}
              seekTo={startValue}
            />
          </div>
          <div className="page--video-detail__spotlight"/>
        </div>
        {/* <div className="page--video-detail__multi-range">
          <input
            type="range"
            ref={this.startInputRef}
            min={startMin}
            max={endMax}
            id="start"
            value={startValue}
            className="page--video-detail__trimming-start"
            onChange={this.handleDragStart}
          />
          <input
            type="range"
            ref={this.endInputRef}
            min={startMin}
            max={endMax}
            id="end"
            value={endValue}
            className="page--video-detail__trimming-end"
            onChange={this.handleDragEnd}
          />
        </div> */}
      </div>
    );
  }
}



