import * as React from "react";
import { LogoFull } from "../components/logo/logo-full";
import { PuiIcon, SizeModifier } from "@pui/react";
import { Link } from "react-router-dom";
import "./less/top-nav.less";

export class TopNav extends React.Component<{
  handleProperty?: (activeProperty: string) => void;
}> {

  state = { 
    menuOpen: false,
    activeProperty: "Apartment Therapy"
  };

  toggleMenu = () => {
    this.setState({ menuOpen: !this.state.menuOpen });
  }

  handleProperty = (e:any) => {
    const activeProperty = e.target.innerHTML;
    this.setState({ activeProperty, menuOpen: false });
    this.props.handleProperty && this.props.handleProperty(activeProperty);
  }

  render() {
    const { menuOpen, activeProperty } = this.state;
    return (
      <div className={`app__top-nav${menuOpen ? ' -is-open' : ''}`}>
        <div className="app__top-nav__main">
          <div className="app__top-nav__logo">
            <Link to="/app">
              <LogoFull fill="#000" />
            </Link>
            <span>friend@moneymakr.me</span>
          </div>
          <div className="app__top-nav__property" onClick={this.toggleMenu}>
            <div className="app__top-nav__property-active">{activeProperty}</div>
            <PuiIcon name={menuOpen ? 'chevron-up' : 'chevron-down'} size={SizeModifier.s} />
          </div>
        </div>
        <div className="app__top-nav__property-dropdown">
          <div className="app__top-nav__property-dropdown-item" onClick={this.handleProperty}>
          { activeProperty === 'Apartment Therapy' ? 'Fader TV' : 'Apartment Therapy'} 
          </div>
        </div>
      </div>
    );
  }
}


