import "./footer.less";
import * as React from "react";

const links = [
  {
    href: "https://www.jwplayer.com/tos",
    name: "Terms of Service"
  },
  {
    href: "https://www.jwplayer.com/privacy/",
    name: "Privacy Policy"
  },
  {
    href: "https://www.jwplayer.com/dmca/",
    name: "DMCA"
  },
  {
    href: "https://www.jwplayer.com/privacy/#ad-choices",
    name: "Ad Choices"
  }
];

export class Footer extends React.Component<{
  theme?: string;
}> {

  render() {
    return (
      <div className={`simi-footer simi-footer--${this.props.theme}`}>
        <ul className="simi-footer__links">
          {links.map((link, i) => {
            return (
              <li key={i}>
                <a href={link.href}>{link.name}</a>
              </li>
            );
          })}
        </ul>
      </div>
    );
  }
}



